.projects-site-container {
  min-height: calc(100vh - 80px) !important;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects-site-container h1 {
  padding-top: 10px;
  padding-bottom: 10px;
}

.projects-container {
  font-size: larger;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  text-align: center;
  margin-bottom: 10px;
}

.projects-cards-container {
  display: flex;
  flex-flow: ro;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.projects-cards-wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.projects-card {
  display: flex;
  flex: 1;
  margin: 0 1rem;
}

.projects-cards {
  margin-bottom: 24px;
}

.projects-card-link {
  display: flex;
  flex-flow: column wrap;
  width: 100%;
  overflow: hidden;
  text-decoration: none;
  color: black;
}

@media only screen and (min-width: 1024px) {
  .projects-cards {
    display: grid;
    grid-template-columns: repeat(2, auto);
    align-items: start;
  }
}

@media only screen and (max-width: 1024px) {
  .projects-card {
    margin-bottom: 2rem;
  }
}
