.about-site-container {
  min-height: calc(100vh - 80px) !important;
  width: 100%;
  display: flex;
  justify-content: center;
}

.about-container {
  font-size: larger;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50vw;
  text-align: center;
  margin-bottom: 10px;
}

.about-container h1,
.about-container h2 {
  padding-top: 10px;
  padding-bottom: 10px;
}

.about-container p {
  padding-top: 10px;
  padding-bottom: 10px;
}

@media only screen and (max-width: 600px) {
  .about-container {
    width: 90vw;
  }
}
