.sec-site-container {
  max-height: calc(100vh - 80px);
  width: 100%;
  display: flex;
  flex-direction: row;
}

.sec-canvas-container {
  min-width: calc(100vw - 535px);
  height: calc(100vh - 120px);
  margin: 20px;
  background-color: white;
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.2);
  cursor: crosshair;
}

.sec-side-container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.sec-controls-container {
  margin: 20px;
  background-color: white;
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 20px;
  border-radius: 10px;
  margin-left: 0px;
}

.sec-controls-btns {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 10px;
}

.sec-controls-switch {
  margin-bottom: 15px;
}

.sec-controls-calc {
  display: flex;
  justify-content: space-around;
  margin-bottom: 5px;
}

.sec-slider-container {
  display: flex;
  justify-content: center;
}

.sec-controls-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.sec-slider {
  width: 97%;
  margin-bottom: 20px;
}

.sec-legend {
  margin: 20px;
  margin-top: 0px;
  margin-left: 0px;
  padding: 15px;
  background-color: white;
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
.sec-side-container strong {
  font-weight: bold;
}

.sec-infocard {
  background-color: white;
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  margin-right: 20px;
  padding: 15px;
  overflow-y: auto;
  max-height: 100%;
  border-radius: 10px;
}

.sec-infocard h1,
.sec-infocard h2 {
  margin-bottom: 5px;
  font-weight: bold;
}

.sec-infocard h2 {
  margin-top: 5px;
}

.sec-infocard p {
  color: rgb(80, 78, 78);
}

.sec-lemma {
  margin-top: 10px;
  margin-bottom: 10px;
  outline-style: solid;
  outline-color: black;
  padding: 5px;
}

.sec-pseudocode {
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  padding: 5px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.sec-indent {
  width: 90%;
  margin-left: 15px;
}

@media screen and (max-width: 960px) {
  .sec-site-container {
    max-height: none;
    display: flex;
    flex-direction: column;
    padding-left: 20px;
  }

  .sec-canvas-container {
    height: calc(50vh);
    margin: 20px;
    margin-left: 0px;
    background-color: white;
    box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.2);
    cursor: crosshair;
  }
}

@media screen and (max-height: 600px) {
  .sec-site-container {
    max-height: none;
    display: flex;
    flex-direction: column;
    padding-left: 20px;
  }

  .sec-canvas-container {
    height: calc(60vh);
    margin: 20px;
    margin-left: 0px;
    background-color: white;
    box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.2);
    cursor: crosshair;
  }
}
