.blog-site-container {
  min-height: calc(100vh - 80px) !important;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: larger;
}

.blog-site-container h1 {
  padding-top: 10px;
  padding-bottom: 10px;
}

.blog-items-container {
  display: flex;
  flex-direction: column;
  max-width: 39vw;
  font-size: medium;
}

.blog-post-item {
  margin: 10px;
  margin-top: 5px;
}

.blog-subText {
  color: rgb(97, 97, 99);
  -webkit-mask-image: linear-gradient(180deg, #000 70%, transparent);
}

.blog-post-container {
  display: flex;
  flex-direction: column;
  width: 50vw;
}

.blog-post-container p {
  margin-bottom: 15px;
}

.blog-post-container code {
  font-family: monospace;
  background-color: lightgrey;
}

.blog-post-container h1 {
  text-align: center;
}

.blog-post-container li {
  margin-left: 18px;
}

@media only screen and (max-width: 600px) {
  .blog-post-container {
    width: 90vw;
  }

  .blog-items-container {
    max-width: 90vw;
  }
}
